import React, { Component } from "react";
import Table from "../../src";
import Checkbox from "bee-checkbox";
import Button from "bee-button";
import multiSelect from "../../src/lib/multiSelect.js";
import sort from "../../src/lib/sort.js";
import sum from "../../src/lib/sum.js";

const columns13 = [
  {
    title: "",
    dataIndex: "a",
    key: "a",
    width: 200
  },
  {
    title: "",
    dataIndex: "b",
    key: "b",
    width: 200
  },
  {
    title: "",
    dataIndex: "c",
    key: "c",
    width: 200,
    sumCol: true,
    sorter: (a, b) => a.c - b.c
  },
  {
    title: "",
    dataIndex: "d",
    key: "d",
    width: 200
  }
];

const data13 = [
  { a: "", b: "", c: 30, d: "", key: "2" },
  { a: "", b: "", c: 41, d: "", key: "1" },
  { a: "", b: "", c: 25, d: "", key: "3" }
];
const data13_1 = [
  { a: "", b: "", c: 30, d: "", key: "2" },
  { a: "", b: "", c: 30, d: "", key: "22" },
  { a: "", b: "", c: 30, d: "", key: "222" },
  { a: "", b: "", c: 41, d: "", key: "1" },
  { a: "", b: "", c: 25, d: "", key: "3" }
];
//tablerenderstate
let ComplexTable = multiSelect(sum(sort(Table)));

class Demo13 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data13: data13,
      selectedRow: this.selectedRow,
      selectDisabled: this.selectDisabled
    };
  }
  getSelectedDataFunc = data => {
    console.log(data)
    this.setState({
      data13:data
    })
  };
  selectDisabled = (record, index) => {
    console.log(record);
    if (index === 1) {
      return true;
    }
    return false;
  };
  selectedRow = (record, index) => {
    // console.log(record);
    // if (index === 0) {
      return true;
    // }
    // return false;
  };
  onClick = () => {
    this.setState({
      selectedRow: function() {}
    });
  };
  onClick1 = () => {
    this.setState({
      selectDisabled: (record, index) => {
        console.log(record);
        if (index === 2) {
          return true;
        }
        return false;
      }
    });
  };
  render() {
    let multiObj = {
      type: "checkbox"
    };
    return (
      <div>
        <Button className="editable-add-btn" onClick={this.onClick}>
          change selectedRow
        </Button>
        <Button
          className="editable-add-btn"
          style={{ marginLeft: "5px" }}
          onClick={this.onClick1}
        >
          change selectDisabled
        </Button>
        <ComplexTable
          // selectDisabled={this.state.selectDisabled}
          selectedRow={this.state.selectedRow}
          columns={columns13}
          data={this.state.data13}
          multiSelect={multiObj}
          getSelectedDataFunc={this.getSelectedDataFunc}
        />
      </div>
    );
  }
}
export default Demo13;



